.main{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);

    width: 25vw;
    height: 30vh;

    z-index: 100;

    background-color: #e5808c;
    border-radius: 7px;
}

.container{
    width: 100%;
    height: 100%;

    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.deletetext{
    width: 100%;
    height: 50%;

    display: flex;
    justify-content: center;
    align-items: center;

    font-size: 1.5rem;
}

.buttonbox{
    position: relative;

    width: 100%;
    height: 30%;

    display: flex;
    justify-content: center;
    align-items: center;
    gap: 100px;
}


.yesnobutton{
    width: 100px;
    padding: 10px 20px;

    display: flex;
    justify-content: center;
    align-items: center;

    border:none;
    cursor: pointer;

    border-radius: 4px;
    background-color: white;

    font-size: 1.2rem;
}