.name_box{
    position: relative;
    width: 100%;

    bottom: 20px;

    justify-items: center;
    
}

.inputboxname{
    width: 200px;
    height: 50px;

    align-content: center;

    font-size: 1.2rem;
}

.input_container{
    position: relative;
    width: 200px;
    height: 35px;
    /* background-color: black; */
}

.input_container::after {
    content: "";
    position: absolute;
    left: 0;
    bottom: 15px;                       /*구분과 선의 줄*/
    width: 100%;
    height: 2px;
    background-color: #f0b549;
}

.inputbox{
    background: transparent;
    border: none;  
    outline: none; 
    color: #221e1f;  /* 입력된 텍스트 색상 */
}