.nav {
  position: fixed;
  left: 0;
  top: 0;
  width: 15vw;
  height: 100vh;
  background-color: #292b46;
  /* padding-left: 3.75vw; */
  box-sizing: border-box;
}

.container {
  display: flex;
  flex-direction: column;
  position: absolute;
  align-items: right;

  width: 10vw;
  top: 40%;
  left: 50%;
  transform: translate(-50%, -50%);
  font-size: 2rem;
}

.container .title {
  font-size: 2rem;
  font-weight: 600;
  color: #f3f2f3;
  /* filter: drop-shadow(2px 2px 10px rgba(243, 242, 243, 0.4)); */
  margin-left: auto;
  margin-bottom: 20px;
}

.content {
  margin-left: auto;
  text-align: right;
  font-size: 1.2rem;
  margin-bottom: 7px;

  * {
    text-decoration: none;
    color: #f3f2f3;
  }

  transition: all 0.3s ease;
}

.content:hover {
  transform: translateY(-2px);
  text-shadow: 0px 2px 4px rgba(255, 255, 255, 0.8);
}

.logout {
  margin-left: auto;
  margin-top: 30px;
  padding: 5px 0;
  box-sizing: border-box;
  width: 100px;

  background-color: #e30c25;
  color: white;
  border: none;
  border-radius: 5px;

  cursor: pointer;

  text-align: center;
  font-size: 1rem;
  font-weight: 600;
}