.main {
    position: absolute;
    left: 15vw;
    max-width: 100%;
    min-height: 100vh;
    
    background: url(../../login/img/login_back.jpg);
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;

    padding: 12.5vw;
    box-sizing: border-box;
}

.blur {
    position: fixed;
    top: 0;
    left: 15vw;
    width: 85vw;
    height: 100vh;

    background-color: rgba(255, 255, 255, 0.4);
}

.title {
    position: absolute;
    top: 100px;
    filter: drop-shadow(0 4px 4px rgba(34, 30, 31, 0.4));
    font-size: 36px;
    font-weight: bold;
}

.btns {
    display: flex;
    position: relative;
}

.registerBtn {
    background-color: #3247fc;
    color: white;
    border: none;
    border-radius: 7px;
    padding: 10px 20px;
    cursor: pointer;

    font-size: 1rem;
}

.searchContainer {
    display: flex;
    margin-left: auto;
}

.input,
.dropdown {
    border-radius: 7px;
    padding: 10px;
    margin-left: 20px;
    border: 1px solid #5162f5;
    background-color: #f5f5f5;

    font-size: 1rem;
}

.input {
    width: 300px;
}

.icon {
    position: absolute;
    top: 50%;
    right: 5px;

    transform: translateY(-50%);

    background-color: transparent;
    border: none;
    cursor: pointer;

    font-size: 1rem;
}

.tableHeader {
    display: flex;
    position: relative;
    padding: 20px 0 20px 5px;
    margin: 30px 0 10px;
    background-color: #c5caf2;
    justify-content: space-evenly;

    font-weight: 600;
    border-top-left-radius: 5px;
    border-top-right-radius: 5px;
}

.tableHeader div {
    width: calc(100% / 8);
    text-align: center;
}

.tableBody {
    table-layout: fixed;
    border-collapse: separate;
    width: 100%;
    text-align: center;
    border-spacing: 0 20px;

    /* background-color: #fff; */
}

.tableBody .edit {
    position: relative;
}

.tableBody .editMenu {
    display: flex;
    position: relative;
    left: 50%;
    transform: translateX(-50%);
    padding: 20px;
    box-sizing: border-box;
    justify-content: space-around;
    align-items: center;
    
    width: 150px;
    height: 40px;
    background-color: #555;
    text-align: center;
    border-radius: 5px;
}

.tableBody .editMenu .modify,
.tableBody .editMenu .delete {
    text-decoration: none;
    color: white;
}

.tableBody .editMenu button:hover {
    background-color: #444;
}

.tableBody .tr {
    position: relative;
    width: calc(100% / 8);
    height: 70px;
    box-shadow: 2px 2px 10px rgba(81, 98, 245, 0.6);
    border: 1px solid #8bd2ef;
    background-color: #f5f5f5;
    border-radius: 5px;
}

.tableBody td:first-child {
    border-top-left-radius: 5px;
    border-bottom-left-radius: 5px;
}

.tableBody td:last-child {
    border-top-right-radius: 5px;
    border-bottom-right-radius: 5px;
}

table img {
    width: 50px;
    height: 50px;
    object-fit: cover;
}

table td button {
    border: none;
    background-color: transparent;
    cursor: pointer;
    font-size: 1.5rem;
    font-weight: 600;
    text-align: center;
}

