.main {
    width: 85vw;
    height: 100vh;
    position: fixed;
    top: 50%;
    left: 57.5%;
    transform: translate(-50%, -50%);
    background: url(../img/modification.png);
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;

    z-index: 99;
}

.background {
    width: 85vw;
    height: 100vh;
}

.container {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);

    /* border: 1px solid #ddd; */
    border-radius: 10px;
    background-color: #ddd;

    width: 600px;
    height: 500px;

    padding: 30px;
    box-sizing: border-box;

    z-index: 100;
}


h2 {
    text-align: center;
    margin-bottom: 30px;

}

.form {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.form .textbox {
    width: 300px;
    height: 30px;

    margin-bottom: 10px;
}

.photo {
    display: flex;
    align-items: center;
    width: 300px;
    height: 30px;
    border: 1px solid #4F4F4F;
    padding: 3px;
    border-radius: 3px;
    margin-bottom: 20px;
}

.btns {
    width: 330px;
    display: flex;
    justify-content: space-around;
}

.btns .cancel,
.btns .submit {
    text-decoration: none;
    background-color: #757CFF;
    color: white;
    border: none;
    border-radius: 5px;
    padding: 7px 40px;
    cursor: pointer;
}